/**
 * Enquiry.js
 * Enquiry view that appears in most of the forms
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
*/
import React, { Component } from 'react';
import { Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Phone from 'assets/images/common/Phone.svg';

import './Enquiry.scss';

class Enquiry extends Component {
  render() {
    const { marginTop } = this.props;

    return (
      <Col className="enquiry-container" style={{ marginTop }}>
        <div className="upper-container">
          <p className="left-text">
            Need some assistance? Please contact us and one of our
            Client Services Executives will be happy to guide you along.
          </p>
          <div className="right-text">
            <img src={Phone} alt="Phone.svg" />
            <a href="tel:+6531639184">
              +65 3163 9184
            </a>
          </div>
        </div>
        <div className="lower-container">
          <p className="lower-main">Speak to our Client Service Executives</p>
          <p className="lower-sub">Servicing hours:</p>
          <p className="lower-sub">10am - 7pm</p>
          <p className="lower-sub">Mondays to Fridays</p>
          <p className="lower-sub">(excluding public holidays)</p>
        </div>
      </Col>
    );
  }
}

Enquiry.defaultProps = {
  marginTop: 0,
};


Enquiry.propTypes = {
  marginTop: PropTypes.number,
};

export default Enquiry;
