import React, { Component } from 'react';
import { Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Pig from 'assets/images/Motor/Pig.svg';

import Button from 'components/FormsV3/Button/Button';

import './PiggySignUp.scss';

class PiggySignUp extends Component {
  render() {
    const {
      header,
      subHeader,
      buttonInner,
      onClick,
    } = this.props;

    return (
      <div className="piggy-container">
        <Col className="d-flex d-sm-none wrap-container" xs={12}>
          <div className="piggy-img-container">
            <img src={Pig} alt="Pig.svg" />
          </div>
          <div className="piggy-right-container">
            <div className="piggy-desc-container">
              {header}
              {subHeader}
            </div>
          </div>
          <div className="button-container">
            <Button variant="medium-color" width="100%" height={48} onClick={onClick}>
              <p>{buttonInner}</p>
            </Button>
          </div>
        </Col>
        <Col className="d-none d-sm-flex wrap-container" xs={12}>
          <div className="piggy-img-container">
            <img src={Pig} alt="Pig.svg" />
          </div>
          <div className="piggy-right-container">
            <div className="piggy-desc-container">
              {header}
              {subHeader}
            </div>
            <div className="button-container">
              <Button variant="medium-color" width="100%" height={48} onClick={onClick}>
                <p>{buttonInner}</p>
              </Button>
            </div>
          </div>
        </Col>
      </div>
    );
  }
}

PiggySignUp.defaultProps = {
  onClick: () => {},
};

PiggySignUp.propTypes = {
  header: PropTypes.object.isRequired,
  subHeader: PropTypes.object.isRequired,
  buttonInner: PropTypes.object.isRequired,
  onClick: PropTypes.func,
};

export default PiggySignUp;
