/**
 * Accordion.js
 * A collapse view that is enhanced from reactstrap
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { Collapse, CardBody, Card } from 'reactstrap';
import PropTypes from 'prop-types';

import ArrowGradient from 'assets/images/forms/ArrowGradient.svg';

import './Accordion.scss';

/**
 * Custom Form Dropdown class
 */
class Accordion extends Component {
  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    this.state = {
      isOpened: false,
      openCallback: null,
    };

    this.handleExpand = this.handleExpand.bind(this);
    this.setOpenCallback = this.setOpenCallback.bind(this);
  }

  setOpenCallback(callback) {
    this.setState({
      openCallback: callback,
    });
  }

  handleExpand() {
    const { isOpened, openCallback } = this.state;
    if (!isOpened && openCallback) {
      setTimeout(() => openCallback(), 10);
    }

    this.setState({
      isOpened: !isOpened,
    });
  }

  render() {
    const {
      isOpened,
    } = this.state;

    const {
      height,
      isWarning,
      isFullBorder,
    } = this.props;

    const openStyle = isOpened ? 'accordion-opened' : 'accordion-closed';
    const openText = isOpened ? 'Close Details' : 'Expand Details';
    const arrowOpenStyle = isOpened ? 'arrow-opened' : 'arrow-closed';
    const borderStyle = isFullBorder ? 'accordion-border' : '';

    return (
      <div className={`${borderStyle} accordion-container`}>
        <div className="accordion-inner">
          <Collapse className={openStyle} isOpen={isOpened}>
            <Card>
              <CardBody>
                {this.props.children}
              </CardBody>
            </Card>
          </Collapse>
        </div>
        <div className="expand-container" style={{ minHeight: height }}>
          <button
            className="control-container"
            onClick={() => this.handleExpand()}
          >
            <img className={`arrow ${arrowOpenStyle}`} src={ArrowGradient} alt="Arrow.svg" />
            <span>{openText}</span>
          </button>
        </div>
        {
          isWarning ?
            <div className="warning-container">
              <p>Please make sure all your information is correct before making the payment</p>
            </div>
          :
            null
        }
      </div>
    );
  }
}

Accordion.defaultProps = {
  isWarning: false,
  height: 95,
  isFullBorder: false,
};

Accordion.propTypes = {
  isWarning: PropTypes.bool,
  isFullBorder: PropTypes.bool,
  height: PropTypes.number,
};

export default Accordion;
